
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import DatetimeRange from 'vux/src/components/datetime-range/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'addCoupons',
components: {
XButton,
XInput,
Group,
DatetimeRange
},
data () {
return {
startDate: '2017-01-01',
endDate: '2020-02-02',
value: ['2017-01-15', '03', '05'],
params: {
amount: '',
lowerLimitAmount: '',
buildQuantity: '',
startTime: [],
endTime: []
}
}
},
methods: {
getBack () {
this.$router.go(-1)
},
onChangeStart (val) {
console.log('change', val)
this.params.startTime = val
},
onChangeEnd (val) {
console.log('change', val)
this.params.endTime = val
},
postData () {
this.$vux.loading.show({
text: '...'
})
let params = JSON.parse(JSON.stringify(this.params))
params['startTime'] = params.startTime.join('')
params['endTime'] = params.endTime.join('')
params['merchantId'] = this.$store.state.merchantId
this.$http.fetchPost('/merchant/post/add/coupon', params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '',
position: 'middle'
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
}
}



